.main_banner {
    height: 432px;
    max-width: 1440px;
    margin: 0 auto;
    padding: 112px 0;
    background-image: linear-gradient(rgb(46, 47, 66, 0.7), rgb(46, 47, 66, 0.7)), url(../images/banner_mobile.jpg);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    text-align: center;
    h1 {
        margin: 0 auto 48px auto;
        font-size: 36px;
        line-height: 1.1;
        font-weight: 700;
        letter-spacing: 0.02em;
        color: #fff;
        
        @include respond(xs) {
            width: 319px;
        }
        @include respond(md) {
            width: 496px;
            font-size: 56px;
            line-height: 1.07;
        }
    }
    .btn_primary {
        padding: 16px 32px;
        color: #fff;
        background: $primary;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
        font-weight: 500;
        font-size: 16px;
        line-height: 1.5;
        letter-spacing: 0.04em;
        border-radius: 4px;
        border: none;
        cursor: pointer;
        transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
        &:hover,
        &:focus {
            background-color: $primary-dark;
        }
    }
    @include img-2x {
        background-image: linear-gradient(rgb(46, 47, 66, 0.7), rgb(46, 47, 66, 0.7)), url('../images/banner_mobile2x.jpg');
    }
    @media screen and (min-width: 481px) {
        background-image: linear-gradient(rgb(46, 47, 66, 0.7), rgb(46, 47, 66, 0.7)), url(../images/banner_tablet.jpg);
        @include img-2x {
            background-image: linear-gradient(rgb(46, 47, 66, 0.7), rgb(46, 47, 66, 0.7)), url(../images/banner_tablet2x.jpg);
        }
    }
    @include respond(lg) {
        height: 600px;
        padding: 187px 0;
    }
    @media screen and (min-width: 769px) {
        background-image: linear-gradient(rgb(46, 47, 66, 0.7), rgb(46, 47, 66, 0.7)), url(../images/banner_desktop.jpg);
        @include img-2x {
            background-image: linear-gradient(rgb(46, 47, 66, 0.7), rgb(46, 47, 66, 0.7)), url(../images/banner_desktop2x.jpg);
        }
    }
}